.wrapper {
  display: flex;
  flex-direction: column;
  width: 42%;
  height: 100%;
  justify-content: space-between;
  transition: 0.4s;
}

.item {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-size: 1.5em;
  line-height: 1em;
}
.value {
  font-weight: 500;
}
